<template>
    <div>
        <h2>注册</h2>
        <div id="reg-content">
            <div>用<span id="reghu">户</span>名:<input type="text" v-model="regname" id="regname" name="regname" placeholder="请输入用户名"/></div>
            <div>设置密码:<input type="password" v-model="regpassword1" id="regpassword1" name="regpassword" placeholder="请输入密码"/></div>
            <div>确认密码:<input type="password" v-model="regpassword2" id="regpassword2" name="regpassword" placeholder="请再输入密码"/></div>
            <div>手机号码:<input type="text" v-model="regphoneNum" id="regphoneNum" name="regphoneNum" placeholder="请输入手机号" maxlength="11"/></div>
            <div>
                <input type="text" v-model="regCod" id="regCod" name="regCod" placeholder="短信验证码" maxlength="6"/>
                <button id="getCode" @click="reggetCod">获取短信验证码</button>
            </div>
            <label id="reglab">
                <input type="checkbox" id="regcheck" v-model="ischecked">
                我已阅读并同意相关服务条款和隐私政策{{isregChecked}}
            </label><br/>
            <button id="prompylyRegister"  type="button" @click="goprompylyRegister" v-bind:class="classDis">立即注册</button>
        </div>
    </div>
</template>

<script>
import common from "@/utils/common";
export default {
    name:'register',
    data(){
        return {
            regname:'',
            regpassword1:'',
            regpassword2:'',
            regphoneNum:'',
            regCod:'',
            ischecked:'',
            classDis:'',
        }
    },
    // 计算属性
    computed:{
        // 判断是否选中条款
        isregChecked(){
            if(this.ischecked == true){
                this.classDis = '';
            }else{
                this.classDis = 'disabled';
            }
        }
    },
    methods:{
        reggetCod(){
            common.daojishi('5');
        },
        goprompylyRegister(){
            console.log('立即注册')
            //用户名正则，4到16位（字母，数字，下划线，减号）
            let uPatternName = /^[a-zA-Z0-9_-]{4,16}$/;
            //密码强度正则，最少6位，包括至少1个大写字母，1个小写字母，1个数字，1个特殊字符
            let pPatternPassw = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/;
            //校验手机号
            let uPatternPho = /^((13[0-9])|(14[5|7])|(17[5|7|6])|(15([0-3]|[5-9]))|(18[0,5-9]))\d{8}$/;
            // 效验验证码
            let uPatternCod = /^[0-9]{6}$/;
            // 
            let regpasswd1 = this.regpassword1;
            let regpasswd2 = this.regpassword2;
            console.log(regpasswd1,regpasswd2)

            if (common.isNull(this.regname)) {
                common.altPrompt('用户名不能为空')
                return false
            } else if(uPatternName.test(this.regname) == false){
                common.altPrompt('用户名格式不正确')
                return false
            }else{
                console.log('用户名正确')
            }

            if (common.isNull(this.regpassword1)) {
                common.altPrompt('密码不能为空')
                return false
            } else if(pPatternPassw.test(this.regpassword1) == false){
                common.altPrompt('密码格式不正确')
                return false
            }else{
                console.log('密码正确')
            }

            if (regpasswd1 != regpasswd2) {
                common.altPrompt('两次密码输入不一致，请确认后重新输入')
                return false
            } else {
                console.log('密码一致')
            }

            if (common.isNull(this.regphoneNum)) {
                common.altPrompt('手机号码不能为空')
                return false
            } else if(uPatternPho.test(this.regphoneNum) == false){
                common.altPrompt('手机号码格式不正确')
                return false
            }else{
                console.log('手机号码正确')
            }

            if (common.isNull(this.regCod)) {
                common.altPrompt('验证码不能为空')
                return false
            } else if(uPatternCod.test(this.regCod) == false){
                common.altPrompt('验证码格式不正确')
                return false
            }else{
                console.log('验证码正确')
            }
        }
    }
}
</script>

<style>
    
#getCode{
    width:265px;
    background: #ef4300;
    border: 1px solid #ff730e;
}
</style>



